import {Component} from '@angular/core';
import {NavController, NavParams, ModalController, ViewController, ActionSheetController} from 'ionic-angular';
import { CustomerServiceProvider} from "../../../providers/customer-service/customer-service";
import {AuthServiceProvider} from "../../../providers/auth-service/auth-service";
import {GMap} from "../../gMap/gMap";


@Component({
  selector: 'page-shamelist',
  templateUrl: 'shameList.html',
})
export class ShameList {

  list:any = [];
  totalNum = 0;
  loading:boolean= false;
  salesman:any;
  salesman_name:any;
  totalSales = 0;
  totalVisits = 0;
  math = Math;
  n= Number;

  constructor(public navCtrl: NavController, public navParams: NavParams, public waritex:CustomerServiceProvider , public auth:AuthServiceProvider , public modalCtrl: ModalController , public actionSheetCtrl: ActionSheetController) {
    this.salesman = this.auth.supervisor? this.auth.selectedSalesman : this.auth.getUserInfo();
  }

  ionViewDidLoad() {
    if (this.auth.supervisor && !this.salesman)
      return;
    if (!this.waritex.Areas){
      this.loading = true;
      this.waritex.get_customers_by_areas(this.salesman).subscribe((res) => {
        this.waritex.setAreas(res.res);
        this.getList();
        this.loading = false;
      } , err => {this.loading = false; this.navCtrl.pop()});
    }
    else{
      try {this.getList();} catch (e) {console.log(e)}
    }
  }

  ionViewWillEnter() {
    if (this.auth.supervisor) {
      if (!this.auth.selectedSalesman)
        return;
      this.salesman = this.auth.supervisor ? this.auth.selectedSalesman : this.auth.getUserInfo();
      this.loading = true;
      this.waritex.get_customers_by_areas(this.salesman).subscribe((res) => {
        this.waritex.setAreas(res.res);
        this.getList();
        this.salesman_name = this.auth.getSalesmanName();
        this.loading = false;
      } , err => {this.navCtrl.pop()});
    }
  }

  goNotVisitLocation(marker){
    this.navCtrl.pop();
    this.navCtrl.parent.select(0);
    marker.getMap().setCenter(marker.getPosition());
    marker.getMap().setZoom(17);
  }

  getList(){
    this.list = []
    this.totalNum = 0;
    this.totalSales = 0;
    this.totalVisits = 0;
    Object.keys(this.waritex.Areas).forEach((name) => {
      let list = {
        name: name,
        cus: [],
        num: 0,
        areaMaxSales : 0,
      }
      this.waritex.Areas[name].forEach(e=>{
        if (e.LastInvoiceDate>=90 && e.visited==1){
          list.cus.push(e)
          list.num += 1;
          list.areaMaxSales += parseFloat(e.MaxSales);
          this.totalSales += parseFloat(e.MaxSales);
        }
        if (e.visited==1)
          this.totalVisits += 1;
      })
      if (list.num!==0){
        this.list.push(list)
        this.totalNum += list.num
      }
    })
  }


  areaSelected(item){
    let areaModal = this.modalCtrl.create(ShameModal, { info: item });
    areaModal.present();
  }

  goMap(item){
    this.navCtrl.push(GMap,{areaName: item.name , areaCustomers: item.cus});
  }

  doRefresh(refresher) {
    this.salesman = this.auth.supervisor ? this.auth.selectedSalesman : this.auth.getUserInfo();
    this.waritex.get_customers_by_areas(this.salesman).subscribe((res) => {
      this.waritex.setAreas(res.res);
      this.getList();
      refresher.complete();
    } , err => {refresher.complete(); this.navCtrl.pop()});
  }

  supervisor(){
    let buttons = this.makeButtons();
    const actionSheet = this.actionSheetCtrl.create({
      title: ' ',
      buttons: buttons
    });
    actionSheet.present();
  }

  makeButtons(){
    let buttons = [];
    let salesmans = this.auth.salesmans;
    for(let i=0; i<salesmans.length; i++){
      buttons.push({
        text: salesmans[i].name,
        role: 'destructive',
        handler: () => {
          this.salesman = salesmans[i].code;
          this.salesman_name = salesmans[i].name;
          this.auth.selectedSalesman = this.salesman;
          this.ionViewWillEnter()
        }
      });
    }
    return buttons;
  }

}


/**********************************************************************************/
/**********************************************************************************/
/**
 *  Info Modal
 */
@Component({
  selector: 'page-shamelist',
  templateUrl: 'shameModal.html'
})

export class ShameModal {

  info:any;

  constructor(params: NavParams, public viewCtrl:ViewController , public auth:AuthServiceProvider) {
    this.info = params.get('info');
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

}



