import {Component, ElementRef, ViewChild} from '@angular/core';
import {ActionSheetController, NavController, NavParams} from 'ionic-angular';
import {Db} from "../../providers/db/db";
import {LoadingService} from "../../utils/loading";
import {Geolocation} from '@ionic-native/geolocation';
import {CustomerServiceProvider} from "../../providers/customer-service/customer-service";
import {AuthServiceProvider} from "../../providers/auth-service/auth-service";
import {MapService} from "../../utils/gmap";
import {Observable} from "rxjs";
import {startWith, switchMap} from "rxjs/operators";
import {LocationProvider} from "../../utils/location";


@Component({
  selector: 'page-scanner',
  templateUrl: 'scanner.html'
})
export class ScannerPage {

  @ViewChild('scanner_map') mapRef: ElementRef;
  salesman: any;
  currentArea: any;
  tracking: boolean = false;
  trSub: any;
  syncing: any;
  notes: string = '';
  timeout: any;
  gettingGps: any;

  ameen: boolean = true;
  scanner: boolean = true;
  scannerc: boolean = false;
  sb: boolean = true;

  constructor(public navCtrl: NavController ,public navParams: NavParams, public db: Db, public ld: LoadingService ,
              public geo:Geolocation ,public cus:CustomerServiceProvider , public auth: AuthServiceProvider ,
              public map : MapService , public loc: LocationProvider,
              public actionSheetCtrl: ActionSheetController
  ) {
    this.salesman = this.auth.getUserInfo();
    this.currentArea = this.navParams.get('area');
  }

  ionViewDidLoad(){
    try{
      this.map.setMapRef(this.mapRef);
      this.map.setArea(this.currentArea);
      this.map.salesman = this.salesman;
      this.map.showMap();
      this.map.pollingCustomersData(false);
      this.pollingNewCustomersData(this.cus.CurrentArea,true);
      this.map.pollingAmeenData(this.cus.CurrentArea , {ameen:true});
      this.sync();
      this.watchGeo();
      const s = this.map.rdyToFetch.asObservable().subscribe((r)=>{
        console.log('r: = ',r)
        if (r){
          this.todayMapToRoad();
          s.unsubscribe();
        }
      })
      // this.changeIconSize()
    }
    catch (e) {
      console.log(e)
    }
  }

  ngOnDestroy() {
    try {
      try {
        this.map.clearGmapService();
      }catch (e) {
        console.log(e)
      }
      this.map.pollingc.unsubscribe();
      this.map.pollingc = new Observable();
      this.ld.dismiss();
      // this.loc.watchLoc.unsubscribe();
      this.syncing.unsubscribe();
      this.gettingGps.unsubscribe();
      this.map.currentPos = null;
      this.trSub.unsubscribe();
      this.cus.setCurrentArea(null)
    }
    catch (e) {
      console.log(e)
    }
  }

  sync(){
    this.syncing = Observable.interval(60000)
      .pipe(
        startWith(0),
        switchMap(() => {
          console.log('try Sending...')
          this.notify('sending readings',1);
          return this.db.getDatabaseState();
        })
      )
      .subscribe(
        rdy => {
          if (rdy){
            this.db.getReadings()
              .then((x: Array<any>)=>{
                if (x.length > 0)
                  this.cus.sendReading(x).subscribe(s=>{
                    this.db.syncReadings().then((f)=>{
                      this.notify('readings sent done.')
                    })
                  })
                else
                  this.notify('Nothing to Send.')
              })
              .catch((x)=>{console.log(x);this.ld.dismiss();this.notify('Error in sending readings');})
          }
          else
            this.notify('Error: DB not ready Or Permissions denied!')
        }, er => {this.ld.dismiss(); this.notify('Error in sending readings')}
        , () => {this.ld.dismissall()}
      );
  }

  watchGeo(){
    this.trSub = this.loc.wb.asObservable().subscribe((v)=>{
      if (v > 1) {
        this.showMovement(this.loc.currentLocation);
      }
      console.log('Reading Data from scanner---: ',v);
    })
    this.map.map.addListener("dragend", (xx) => {
      this.tracking = true;
    });
  }


  private showMovement(d){
    this.map.showMove({lat:d.latitude , lng:d.longitude })
    if (this.tracking===false){
      this.map.autoFocus(15);
    }
  }

  recenterViewAll(){
    this.map.autoFocus(15);
    this.tracking = false;
  }

  autoViewAll(){
    this.map.autoViewAll();
    this.tracking = true;
  }


  toggleTrack(){
    if (this.loc.track){
      this.loc.start();
    }
    else {
      this.loc.stop()
    }
  }

  findCustomers(){
    this.loc.getCurrentLoc().subscribe((x : {latitude , longitude})=>{
      let filterd = [];
      this.map.markers.forEach((c)=>{
        let d = this.loc.distance({lat: x.latitude , lon: x.longitude} , {lat:c.getPosition().lat() , lon:c.getPosition().lng()})
        d < 100 ? filterd.push(c) : false
      })
      let btns = [];
      for (let i=0 ; i<filterd.length ; i++){
        btns.push({
          text: filterd[i].title,
          // role: 'destructive',
          handler: () => {
            try {
              filterd[i].getMap().setCenter(filterd[i].getPosition());
              filterd[i].getMap().setZoom(17);
            }catch (e) {

            }
          }
        })
      }
      const actionSheet = this.actionSheetCtrl.create({
        title: '   :',
        buttons: btns
      });
      actionSheet.present();
    })

  }

  todayMapToRoad(){
    this.gettingGps = Observable.interval(60 * 1000)
      .pipe(
        // startWith(10*1000),
        switchMap((x) => {
          console.log('timer: ' ,x);
          this.notify('try fetch new gps readings',1);
          return this.db.getDatabaseState();
        })
      )
      .subscribe(
        rdy => {
          if (rdy){
            let lastGpsNow = this.map.gpsTodayPoints.length > 0 ? new Date(this.map.gpsTodayPoints[this.map.gpsTodayPoints.length-1].datetime).getTime() : new Date().setHours(0,0,0,0);
            console.log('lastGpsNow: ' , lastGpsNow)
            this.db.getNewReadings(lastGpsNow)
              .then((x: Array<any>)=>{
                console.log('xxxxx: ',x)
                if (x.length > 0){
                  var newx = this.map.gpsTodayPoints.length > 0 ? [this.map.gpsTodayPoints[this.map.gpsTodayPoints.length-1] , ...x] : x;
                  console.log('newx: ',newx)
                  var ch = this.map.chunkGPSPoints(newx)
                  var s = []
                  for (var i=0 ; i < ch.length ; i++) {
                    s.push(this.map.prepareRequestPoints(ch[i]))
                  }
                  if( s[0].length > 0 )
                    this.map.handleRequests(s , 'today')
                  // add new points to todayGps's
                  this.map.gpsTodayPoints.push(...x)
                  console.log('Today Points: ',this.map.gpsTodayPoints)
                }
                else
                  this.notify('Nothing New.')
              })
              .catch((x)=>{console.log(x);this.notify('Error in fetching readings');})
          }
        }
      )
  }

  changeIconSize(){
      this.map.map.addListener("zoom_changed", () => {
        try {
          if (this.map.ameen['ameen']){
            if (this.map.map.getZoom() >= 15) {
              this.map.ameen['ameen'].forEach((m)=>{
                let opt = m.getIcon();
                opt.scaledSize = new this.map.goog.maps.Size(20, 20);
                m.setIcon(opt)
              })
            }
            else{
              this.map.ameen['ameen'].forEach((m)=>{
                let opt = m.getIcon();
                opt.scaledSize = new this.map.goog.maps.Size(16, 16);
                m.setIcon(opt)
              })
            }
          }

          if (this.map.ameen['newcus']){
            if (this.map.map.getZoom() >= 15) {
              this.map.ameen['newcus'].forEach((m)=>{
                let opt = m.getIcon();
                opt.scaledSize = new this.map.goog.maps.Size(20, 20);
                m.setIcon(opt)
              })
            }
            else{
              this.map.ameen['newcus'].forEach((m)=>{
                let opt = m.getIcon();
                opt.scaledSize = new this.map.goog.maps.Size(16, 16);
                m.setIcon(opt)
              })
            }
          }
        }
        catch (e) {
          console.log(e)
        }
      });
  }

  toggleView(type){
    switch (type) {
      case 'ameen': {
        console.log('change: ' , type)
        if (this.ameen){
          this.map._showMarkers(this.map.ameen.ameen)
        }
        else {
          this.map._hideMarkers(this.map.ameen.ameen)
        }
      }
        break;
      case 'sb' : {
        console.log('change: ' , type)
        if (this.sb){
          this.map._showMarkers(this.map.ameen.newcus)
        }
        else {
          this.map._hideMarkers(this.map.ameen.newcus)
        }
      }
        break;
      case 'scanner' : {
        console.log('change: ' , type)
        if (this.scanner){
          this.map._showPolyline('past')
          this.map._showPolyline('today')
        }
        else {
          this.map._hidePolyline('past')
          this.map._hidePolyline('today')
        }
      }
        break;
      case 'scannerc' : {
        console.log('change: ' , type)
        if (this.scannerc){
          this.map._showMarkers(this.map.markers);
        }
        else {
          this.map._hideMarkers(this.map.markers);
        }
      }
    }
  }

  pollingNewCustomersData(area , showMarkers : boolean = true){
    let salesman = this.salesman;
    this.map.pollingsc = Observable.interval(60*1000)
      .pipe(
        startWith(0),
        switchMap(() => {
          console.log('load n customers');
          // this.ld.lpresent('ncustomers').then(()=>{console.log('load n customers');});
          return this.map.waritex.get_new_customers_by_areas(salesman , area);
        })
      )
      .subscribe(
        res => {
          this.map.scannerCus = res.scanner;
          const showed = this.sb ? this.map.map : null;
          this.map.showMarkers(res.scanner , showed , 'CustomerNameA' , 'newcus','Latitude','Longitude',3);
          // this.ld.ldismiss('ncustomers').then(()=>{console.log('dismiss n customers');});
        }, er => this.ld.ldismiss('ncustomers')
        , () => {}
      );
  }

  /*********************************************************/
  // Private functions for Errors
  /*********************************************************/

  private notify(msg , loading=0){
    clearInterval(this.timeout);
    this.notes = msg;
    if (loading===1){
      var dots = '';
      this.timeout = setInterval(()=>{
        if (dots.length===3)
          dots = '';
        else dots += '.';
        this.notes = this.notes + dots
      },1000)
    }
    else{
      setTimeout(()=>{
        this.notes = '';
      }, 3000)
    }
  }


}



