import {Component, ElementRef, ViewChild} from '@angular/core';
import {NavParams} from 'ionic-angular';
import {LoadingService} from "../../utils/loading";
import {CustomerServiceProvider} from "../../providers/customer-service/customer-service";
import {AuthServiceProvider} from "../../providers/auth-service/auth-service";
import {MapService} from "../../utils/gmap";
import {Observable} from "rxjs";


@Component({
  selector: 'page-scanner',
  templateUrl: 'ameen.html'
})
export class Ameenscanner {

  @ViewChild('scanner_map') mapRef: ElementRef;
  salesman: any;
  currentArea: any;
  notes: string = '';
  timeout: any;
  gettingGps: any;

  constructor(public navParams: NavParams,
              public ld: LoadingService,
              public cus: CustomerServiceProvider,
              public auth: AuthServiceProvider,
              public map: MapService,
  ) {
    this.salesman = this.auth.supervisor? this.auth.selectedSalesman : this.auth.getUserInfo();
    this.currentArea = this.navParams.get('area');
  }

  ionViewDidLoad(){
    try{
      this.map.setMapRef(this.mapRef);
      this.map.setArea(this.currentArea);
      this.map.salesman = this.salesman;
      this.map.showMap();
      this.map.pollingAmeenData(this.cus.CurrentArea);
    }
    catch (e) {
      console.log(e)
    }
  }

  ngOnDestroy() {
    try {
      this.ld.dismiss();
      this.gettingGps.unsubscribe();
      this.map.currentPos = null;
      this.cus.setCurrentArea(null)
      this.map.clearGmapService();
    }
    catch (e) {
      console.log(e)
    }
  }


  autoViewAll(){
    this.map.autoViewAll();
  }



  /*********************************************************/
  // Private functions for Errors
  /*********************************************************/

  private notify(msg , loading=0){
    clearInterval(this.timeout);
    this.notes = msg;
    if (loading===1){
      var dots = '';
      this.timeout = setInterval(()=>{
        if (dots.length===3)
          dots = '';
        else dots += '.';
        this.notes = this.notes + dots
      },1000)
    }
    else{
      setTimeout(()=>{
        this.notes = '';
      }, 3000)
    }
  }


}



