import { HttpClient , HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {catchError} from "rxjs/operators";
import {AlertController} from "ionic-angular";
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import {Config} from "../../utils/config";

@Injectable()
export class CustomerServiceProvider {

  APIURL:string;

  Customers:any;
  Visited:any;
  NotVisited:any;
  Areas:any;
  CurrentArea:any = null;
  Report:any;
  today:any;
  avgs:any

  constructor(public http: HttpClient, private alertCtrl: AlertController) {
    this.APIURL = Config.API_URL;
  }

  /**
   * get Customers in today's route
   * @param salesman : salesman id
   */
  public get_customers(salesman , info=null){
    let url = this.APIURL+'/get_customers';
    return this.http.post(url , {salesman:salesman , info:info} )
      .pipe(catchError((er) => this.handleError(er)))
  }

  /**
   * get customers that don't have location on map
   * @param salesman : salesman id
   */
  public get_noLocation(salesman){
    let url = this.APIURL+'/get_noloc';
    return this.http.post(url , {salesman:salesman} )
      .pipe(catchError((er) => this.handleError(er)));
  }

  public get_car_location(salesman){
    let url = this.APIURL+'/get_car_location';
    return this.http.post(url , {salesman:salesman} )
      .pipe(catchError((er) => this.handleError(er)))
  }

  public get_all_scanner_areas(salesman){
    let url = this.APIURL+'/get_all_scanner_area';
    return this.http.post(url , {salesman:salesman})
      .pipe(catchError((er) => this.handleError(er)))
  }

  public get_customers_by_areas(salesman , area=null){
    let url = this.APIURL+'/get_customers_area';
    let body = area!==null ? {salesman:salesman , area:area} : {salesman:salesman}
    return this.http.post(url , body )
      .pipe(catchError((er) => this.handleError(er)))
  }
  public get_new_customers_by_areas(salesman,area){
    let url = this.APIURL+'/get_Scannerc';
    return this.http.post(url , {salesman:salesman , area:area} )
      .pipe(catchError((er) => this.handleError(er)))
  }

  public get_GPS_by_areas(salesman,area){
    let url = this.APIURL+'/get_gpsreadings';
    return this.http.post(url , {salesman:salesman , area: area} )
      .pipe(catchError((er) => this.handleError(er)))
  }

  public get_GPS_by_areas_admin(salesman,area,dt){
    let url = this.APIURL+'/get_gpsreadings_admin';
    return this.http.post(url , {salesman:salesman , area: area , datetime:dt} )
      .pipe(catchError((er) => this.handleError(er)))
  }

  public get_Ameen_by_areas_admin(salesman,area){
    let url = this.APIURL+'/get_Ameencc';
    return this.http.post(url , {salesman:salesman , area: area} )
      .pipe(catchError((er) => this.handleError(er)))
  }

  public get_report_by_areas(salesman){
    let url = this.APIURL+'/get_report_area';
    return this.http.post(url , {salesman:salesman} )
      .pipe(catchError((er) => this.handleError(er)))
  }

  public saveInfo(info){
    let url = this.APIURL+'/saveinfo';
    return this.http.post(url , {info:info} )
      .pipe(catchError((er) => this.handleError(er)))
  }

  public sendReading(info){
    let url = this.APIURL+'/readings';
    return this.http.post(url , {readings:info} )
      .pipe(catchError((er) => this.handleError(er)))
  }

  public setCustomers(customers){
    this.Customers = customers;
  }
  public setVisited(visited){
    this.Visited = visited;
  }
  public setNotVisited(not_visited){
    this.NotVisited = not_visited;
  }
  public setAreas(areas){
    this.Areas = areas;
  }
  public setReport(report){
    this.Report = report;
  }
  public setToday(today){
    this.today = today;
  }
  public setAvgs(avgs){
    if (avgs == null || avgs==undefined){
      this.avgs = {RemainWokingDays:'x' , AvgShould:'x'}
    } else{
      this.avgs = avgs;
    }

  }
  public setCurrentArea(Carea){
    this.CurrentArea = Carea;
  }

  public getTodayArea(areas=1){
    let cities = [];
    if (areas===1){
      Object.keys(this.Areas).forEach( area => {
        let s = this.Areas[area].find( cus => {
          return cus.LastVisitDate==0
        })
        if (s){
          cities.push(s.city)
        }
      })
    }
    else{
      this.Report.forEach( r => {
        if (r.LastVisit == 1)
          cities.push(r.city)
      })
    }
    this.setToday(cities)
    return cities;
  }

  public isToday(city,areas=1){
    if (areas===1)
      return this.getTodayArea(areas).find( c => c===city)
    else
      return this.getTodayArea(areas).find( c => c===city)
  }

  public get_collections(){
    let url = 'http://waritex.com/R/salary_r';
    return this.http.get(url)
      .pipe(catchError((er) => this.handleError(er)))
  }

  /*********************************************************/
  // Private functions
  /*********************************************************/

  /**
   * handle http errors
   * @param error
   */
  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
      this.showError(error.error.message,-1);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
      this.showError(error.error,error.status);
    }
    // return an observable with a user-facing error message
    // this.showError('Something bad happened; please try again later.',-2);
    return new ErrorObservable(error);
  };

  private showError(text,status) {
    let alert = this.alertCtrl.create({
      title: 'Error Status: '+status,
      subTitle: text,
      buttons: ['OK']
    });
    alert.present();
  }


}



