import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import {Config} from "../../utils/config";

class User {
  code:string;
  username:string;
  name:string;
  supervisor:boolean;
  carcode:any;
  currency:string;

  constructor(username , code , name , currency = '$' , supervisor=false , carcode=null ){
    this.code = code;
    this.username = username;
    this.name = name;
    this.supervisor = supervisor;
    this.carcode = carcode;
    this.currency = currency;
  }
}

@Injectable()
export class AuthServiceProvider {

  APIURL:string;

  user:User;
  currentUser:string; // Logged in User
  supervisor:boolean = false;
  salesmans:any;
  selectedSalesman:any; // Selected Salesman from Supervisor
  currentType:any;  // sales or scanner

  constructor(public http: HttpClient) {
    this.APIURL = Config.API_URL;
  }

  public login(username, password){
    if (username===null || username===undefined || username===""){
      return Observable.throw("Please insert credentials");
    }
    else {

      return Observable.create(observer => {
        // At this point make a request to your backend to make a real check!
        this.http.post(this.APIURL+'/login' , {username: username})
          .subscribe((user: User) => {
            this.currentUser = user.code;
            this.user = user;
            if(user.supervisor){
              this.supervisor = true;
              this.http.post(this.APIURL+'/get_all_salesman' , {code: user.code})
                .subscribe(
                  (salesmans) => {
                    this.salesmans = salesmans;
                    let access = ( user );
                    observer.next(access);
                    observer.complete();
                  }
                , er => observer.error(er.error.message)
                )
            }
            else{
              let access = ( user );
              observer.next(access);
              observer.complete();
            }
          } , er => observer.error(er.error.message) );
      });
    }
  }

  public getUserInfo() {
    return this.currentUser;
  }

  public getSalesmanName(code=null){
    if (code===null){
      try {return this.salesmans.find( s => {return s.code == this.selectedSalesman}).name} catch (e) {}
    }
    else {
      try {return this.salesmans.find( s => {return s.code == code}).name} catch (e) {}
    }

  }

  public isSupervisor(){
    return this.supervisor;
  }

  public getUserCurrency(){
    return this.user.currency;
  }

  public logout() {
    return Observable.create(observer => {
      this.currentUser = null;
      observer.next(true);
      observer.complete();
    });
  }

}



