import {Injectable} from '@angular/core';
import {Loading, LoadingController} from "ionic-angular";

@Injectable()
export class LoadingService {
  loader: Loading;
  loaders: Record<string, any> = {};

  constructor(public loadingController: LoadingController) {
  }

  async present() {
    this.loader = this.loadingController.create({
      duration: 0,
    })
    return await this.loader.present();
  }

  async dismiss() {
    try {
      return await this.loader.dismiss();
    }
    catch (e) {
      console.log('Loading...', e)
    }
  }

  async dismissall() {
    try {
      return await this.loader.dismissAll();
    }
    catch (e) {
      console.log('Loading...', e)
    }
  }


  async lpresent(obj) {
    this.loaders[obj] = this.loadingController.create({
      duration: 0,
    })
    return await this.loaders[obj].present();
  }

  async ldismiss(obj) {
    try {
      return await this.loaders[obj].dismiss();
    }
    catch (e) {
      console.log('Loading...' + obj, e)
      return await this.loaders[obj].dismissAll();
    }
  }


}



