import {Component} from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { ScheduleProvider } from "../../../providers/schedule/schedule-service";
import { AuthServiceProvider } from "../../../providers/auth-service/auth-service";


@Component({
  selector: 'page-info',
  templateUrl: 'schedule.html',
})
export class Schedule {

  schedule:any;
  toweek:string;
  today:string;
  salesman:any;
  loading:boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, public scheduleService:ScheduleProvider , public auth:AuthServiceProvider) {
    this.salesman = this.auth.supervisor? this.auth.selectedSalesman : this.auth.getUserInfo();
  }

  ionViewDidLoad() {
    this.get_schedule();
  }
  ionViewWillEnter(){
    this.schedule = null;
    this.salesman = this.auth.supervisor? this.auth.selectedSalesman : this.auth.getUserInfo();
    this.get_schedule();
  }

  get_schedule(){
    this.loading = true;
    return this.scheduleService.get_schedule(this.salesman)
      .subscribe(res=>{
        this.schedule = res.route;
        this.toweek = res.week;
        this.today = res.day;
        this.loading = false;
      }, err => {this.loading = false; this.navCtrl.pop()});
  }


  get_areas(day){
    let str = '';
    let i=0;
    for (let item of day){
      i++;
      if (item['Region']!=''){
        str = item['Region'];
        break;
      }
      let ar = item['Area'];
      i===1 ? str = str + ar : str = str + ' - ' + ar
    }
    return str;
  }


}



