import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AuthServiceProvider } from "../../../providers/auth-service/auth-service";
import { Config } from "../../../utils/config";
import { DomSanitizer } from "@angular/platform-browser";

@Component({
  selector: 'page-cash',
  templateUrl: 'cash.html'
})
export class Cash {

  loading:boolean = false;
  salesman:any;
  APIURL:any;
  surl:any;
  firstV : boolean = true;
  params:any = {salesman: null};

  getAPIURL(){
    this.APIURL = Config.R_URL+'/salary_r';
  }

  constructor(public navCtrl: NavController , public auth: AuthServiceProvider , public sanitizer: DomSanitizer ) {
    this.getAPIURL();
    this.setSalesman();
    var str = Object.keys(this.params).map(key => `${key}=${this.params[key]}`).join("&");
    this.surl = this.sanitizer.bypassSecurityTrustResourceUrl(this.APIURL+'?'+str)
  }

  ionViewWillEnter(){
    this.clearSalesman();
    this.setSalesman();
    var str = Object.keys(this.params).map(key => `${key}=${this.params[key]}`).join("&");
    if (this.firstV===true)
      this.firstV = false;
    else if (this.firstV===false)
      this.surl = this.sanitizer.bypassSecurityTrustResourceUrl(this.APIURL+'?'+str)
  }

  ionViewDidLeave(){
    this.surl = '';
  }

  clearSalesman(){
    this.params = {salesman: null}
  }

  setSalesman(){
    this.salesman = this.auth.supervisor? this.auth.selectedSalesman : this.auth.getUserInfo();
    this.params.salesman = this.salesman;
  }

}



