import { Component } from '@angular/core';
import {ActionSheetController, NavController} from 'ionic-angular';
import {AuthServiceProvider} from "../../providers/auth-service/auth-service";
import {CustomerServiceProvider} from "../../providers/customer-service/customer-service";


@Component({
  selector: 'page-report',
  templateUrl: 'report.html'
})
export class Report {

  salesman:any;
  salesman_name:any;
  loading:boolean = false;
  report:any;
  Math = Math;
  n = Number;
  totalSales : any=0;
  totalInv : any=0;
  currentSales:any=0;
  currentInv:any=0;

  constructor(public navCtrl: NavController , public auth:AuthServiceProvider , public waritex:CustomerServiceProvider , public actionSheetCtrl: ActionSheetController) {
    this.salesman = this.auth.supervisor? this.auth.selectedSalesman : this.auth.getUserInfo();
  }

  ionViewWillEnter(){
    this.salesman = this.auth.supervisor? this.auth.selectedSalesman : this.auth.getUserInfo();
    this.totalSales=0;
    this.totalInv=0;
    this.currentSales=0;
    this.currentInv=0;
    this.getData();
  }

  getData(){
    if (this.auth.supervisor && !this.auth.selectedSalesman)
      return;
    this.loading = true;
    this.waritex.get_report_by_areas(this.salesman).subscribe((res) => {
      this.waritex.setReport(res);
      this.report = res;
      this.salesman_name = this.auth.getSalesmanName();
      this.loading = false;
      this.calcTotal();
    } , err => {this.loading = false; this.navCtrl.pop()})
  }

  calcTotal(){
    for (let i=0; i<this.report.length;i++){
      this.totalSales   =   this.totalSales + Math.round(this.report[i].maxtotal);
      this.totalInv     =   this.report[i].maxSalesInv ? this.totalInv + parseInt(this.report[i].maxSalesInv) : this.totalInv + Math.round(parseInt(this.report[i].invoiceNo));
      this.currentSales =   this.currentSales + Math.round(this.report[i].currentSales);
      this.currentInv   =   this.currentInv + Math.round(this.report[i].currentInv);
    }
  }

  supervisor(){
    let buttons = this.makeButtons();
    const actionSheet = this.actionSheetCtrl.create({
      title: ' ',
      buttons: buttons
    });
    actionSheet.present();
  }

  makeButtons(){
    let buttons = [];
    let salesmans = this.auth.salesmans;
    for(let i=0; i<salesmans.length; i++){
      buttons.push({
        text: salesmans[i].name,
        role: 'destructive',
        handler: () => {
          this.salesman = salesmans[i].code;
          this.salesman_name = salesmans[i].name;
          this.auth.selectedSalesman = this.salesman;
          this.ionViewWillEnter()
        }
      });
    }
    return buttons;
  }


}



