  stopPollingInBackground(){
    this.platform.ready().then(() => {
      if (this.platform.is('cordova') || this.platform.is('android')){
        //Subscribe on pause
        this.platform.pause.subscribe(() => {
          this.polling.unsubscribe();
          this.dismissLoading();
        });
        //Subscribe on resume
        this.platform.resume.subscribe(() => {
          this.salesman = this.auth.getUserInfo();
          if(this.navCtrl.getActive().name === 'Waritex' && confirm('Refresh Again?')){
            this.pollingDate();
          }
        });
      }
    });
  }

  presentLoading() {
    const loader = this.loadingCtrl.create({
      content: "Please wait...",
      duration: 1000
    });
    loader.present();
    var th = this;
    loader.onDidDismiss(function () {
      th.ionViewDidLoad()
    })
  }

  // attach info label to marker (customer)
  attachInfo(marker, customer) {
    var s = this.makeContent(customer);
    var place = '<div dir="rtl" style="float: left;">&nbsp; &nbsp;<a href="https://www.google.com/maps/search/?api=1&query=' + customer.Lat + ',' + customer.Lng + '" target="_blank"> </a></div>';
    var infowindow = new google.maps.InfoWindow({
      content: '' +
        '<div><h4 style="float:right; margin-top: 0;" dir="rtl"> ' + customer.CustomerName + '&nbsp;&nbsp;&nbsp;</h4>' +
        place +
        '</div>' +
        '<div>'+ s +'</div>'
    });
    let flag = this;
    infowindow.addListener('closeclick' , function () {
      flag.activeInfoWindowMarker = null;
      flag.activeInfoWindow = null;
    });
    marker.addListener('click', function () {
      if (flag.activeInfoWindow) {
        flag.activeInfoWindow.close();
        flag.activeInfoWindowMarker = null;
        flag.activeInfoWindow = null;
      }
      infowindow.open(marker.get('map'), marker);
      if (!flag.auth.supervisor)
        flag.saveOpenInfo(customer);
      flag.activeInfoWindow = infowindow;
      flag.activeInfoWindowMarker = marker
    });
  }

  saveOpenInfo(customer){
    let cusName = customer.CustomerID;
    let salesman = this.salesman;
    let datetime = new Date().toISOString();
    let s = [{cus_id:cusName , salesman_id: salesman , date_time: datetime}];
    try {
      this.storage.get('clk').then((info)=>{
        if (info!=null)
          s = s.concat(info);
        this.storage.set('clk',s).then((info)=>{
          this.uploadInfo(s)
        })
      })
    }
    catch (e) {
      console.log(e);
      this.storage.set('clk',s).then((info)=>{
        this.uploadInfo(s)
      })
    }
  }

  uploadInfo(info){
    this.waritex.saveInfo(info)
      .subscribe((res)=>{
        this.clearInfo();
    })
  }

  clearInfo(){
    this.storage.clear().then(()=>{

    })
  }

