  showError(text , status=0) {
    this.dismissLoading();

    let alert = this.alertc.create({
      title: 'Error',
      subTitle: text,
      buttons: ['OK']
    });
    alert.present();
  }

  showLoading(dialog=false) {
    if (dialog){
      this.loading = this.loadingCtrl.create({
        content: 'Please wait...',
        dismissOnPageChange: true
      });
      this.loading.present();
    }
    this.loadings = true;
  }

  dismissLoading(all=false){
    this.loadings = false;
    try {
      this.loading.dismiss();
      if (all)
        this.loading.dismissAll();
    }
    catch (e) {

    }
  }

  myLocation(){
    try {
      this.geolocation.getCurrentPosition().then((resp) => {
        // resp.coords.latitude
        // resp.coords.longitude
        alert(resp.coords.latitude);
        var locmypos = new google.maps.LatLng(resp.coords.latitude, resp.coords.longitude);
        new google.maps.Marker({
          map: this.map,
          position: locmypos,
          title: 'My Location',
          label: 'My Location',
        });
        this.bounds.extend(locmypos);
        this.map.fitBounds(this.bounds);       // auto-zoom
        this.map.panToBounds(this.bounds);     // auto-center
      }).catch((error) => {
        console.log('Error getting location', error.message);
      });
    }
    catch (e) {
      this.showError(e);
    }
  }

  pollingDate(){
    if (this.auth.supervisor && !this.selectedSalesman)
      return;
    let salesman = this.auth.supervisor? this.selectedSalesman : this.salesman;
    this.polling = Observable.interval(AutoRefresh)
      .pipe(
        startWith(0),
        switchMap(() => {
          this.showLoading();
          return this.waritex.get_customers(salesman);
        })
      )
      .subscribe(
        res => {
          this.customers = res;
          this.dismissLoading();
          console.log('route done auto refresh');
          this.clearMarkers();
          this.showCustomers();
          this.reOpenInfoWindow();
          if (this.firstShow === 0) {
            this.firstShow++;
            this.autoViewAll();
          }
          this.getAreaInRoute();
        }, er => this.dismissLoading()
        , () => this.dismissLoading(true)
      );
  }

  supervisor(){
    let buttons = this.makeButtons();
    const actionSheet = this.actionSheetCtrl.create({
      title: ' ',
      buttons: buttons
    });
    actionSheet.present();
  }

  makeButtons(){
    let buttons = [];
    let salesmans = this.auth.salesmans;
    for(let i=0; i<salesmans.length; i++){
      buttons.push({
        text: salesmans[i].name,
        role: 'destructive',
        handler: () => {
          this.selectedSalesman = salesmans[i].code;
          this.auth.selectedSalesman = this.selectedSalesman;
          if (this.polling){
            this.polling.unsubscribe();
            this.polling = new Observable();
            try {
              this.dismissLoading();
              this.activeInfoWindow.close();
              this.activeInfoWindowMarker = null;
              this.activeInfoWindow = null;
            }catch (e) {}
          }
          this.firstShow = 0;
          this.pollingDate();
        }
      });
    }
    return buttons;
  }

  car(){
    if (this.auth.supervisor && !this.selectedSalesman)
      return;
    let salesman = this.auth.supervisor? this.selectedSalesman : this.salesman;
    this.waritex.get_car_location(salesman).subscribe(
      car_data => {
        this.carLocation = car_data;
        let latLong = new google.maps.LatLng(this.carLocation.lat, this.carLocation.lng);
        if (!this.carOnMap===undefined){
          this.carOnMap.setMap(null);
        }
        try {
          this.carOnMap.setMap(null);
        }
        catch (e) {
          
        }
        this.carOnMap = new google.maps.Marker({
          position: latLong
        });
        this.carOnMap.setMap(this.map);
      }
      , error=>this.dismissLoading()
      , () => this.dismissLoading(true))
  }

  getAreaInRoute(){
    try {
      let customers = this.customers;
      let citys = '';
      let city =customers.map(item => item.city)
        .filter((value, index, self) => self.indexOf(value) === index)
      city.forEach((e)=> citys+=e+'  -  ')
      return citys;
    }catch (e) {

    }
  }


  // Maps Functions
  /******************************************************************/
