import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { VisitedPage} from "./visited/visited";
import {NolocationPage} from "./noLocation/nolocation";
import { Schedule } from "./schedule/schedule";
import {AboutPage} from "../about/about";
import {ShameList} from "./shameList/shameList";


@Component({
  selector: 'page-info',
  templateUrl: 'info.html'
})
export class InfoPage {

  constructor(public navCtrl: NavController) {

  }

  ionViewWillEnter(){

  }

  goNoLocation(){
    this.navCtrl.push(NolocationPage);
  }

  goVisited(){
    this.navCtrl.push(VisitedPage);
  }

  goSchedule(){
    this.navCtrl.push(Schedule);
  }

  goAbout(){
    this.navCtrl.push(AboutPage);
  }

  goShameList(){
    this.navCtrl.push(ShameList);
  }


}



