import {Injectable} from '@angular/core';
import {
  BackgroundGeolocation,
  BackgroundGeolocationConfig, BackgroundGeolocationCurrentPositionConfig,
  BackgroundGeolocationEvents, BackgroundGeolocationLocationProvider
} from "@ionic-native/background-geolocation";
import {BehaviorSubject, Observable} from "rxjs";
import {Storage} from "@ionic/storage";
import {Db} from "../providers/db/db";
import {AuthServiceProvider} from "../providers/auth-service/auth-service";

@Injectable()
export class LocationProvider{

  bk:BackgroundGeolocation; // background plugin
  public locReady: BehaviorSubject<boolean> = new BehaviorSubject(false); // plugin configured and ready
  public track : boolean = false; // tracking geo_locations start
  public currentLocation: any;  // current location data after start tracking
  public watchLoc: Observable<any>; // Observable of Event : Location changed
  public wb : BehaviorSubject<any> = new BehaviorSubject(1);

  constructor(public background: BackgroundGeolocation ,
              public storage: Storage,
              public db: Db,
              public auth: AuthServiceProvider
              ){

  }

  public getBg() : BackgroundGeolocation{
    return this.bk;
  }

  public start(){
    this.watchGeolocation();
    this.bk.start()
    this.track = true;
    return this.storage.set('startbg',true)
  }

  public stop(){
    this.track = false;
    this.storage.set('startbg',false)
    this.bk.removeAllListeners().then(()=>{})
    this.bk.removeAllListeners(BackgroundGeolocationEvents.location).then(()=>{})
    this.watchLoc = new Observable();
    return this.bk.stop()
  }

  public getLocationState() {
    return this.locReady.asObservable();
  }

  public config(){
    const background = this.background
    background.checkStatus().then((status)=>{
      console.log(status)
    })

    const config: BackgroundGeolocationConfig = {
      locationProvider: BackgroundGeolocationLocationProvider.DISTANCE_FILTER_PROVIDER,
      desiredAccuracy: 0,
      stationaryRadius: 5,
      distanceFilter: 10,
      debug: true, //  enable this hear sounds for background-geolocation life-cycle.
      stopOnTerminate: true, // enable this to clear background location settings when the app terminates
      fastestInterval: 4000,
      interval: 8000,
      // activitiesInterval: 4000,
    };
    background.configure(config)
      .then((l)=>{
        console.log(l)
        this.bk = background;
        background.finish();
        this.locReady.next(true);
      })
      .catch((e)=>{console.log(e)})

    background.headlessTask(function(event) {
      console.log(event)
      if (event.name === 'location' ||
        event.name === 'stationary') {
        console.log(event)
      }

      return 'Processing event: ' + event.name; // will be logged
    });

  }

  public getStorage(){
    this.storage.get('startbg').then((v)=> {
      console.log('storage value: ', v)
      return v;
    })
  }

  public storeGps(data){
    console.log('Saving GPS...');
    data.salesman = this.auth.getUserInfo();;
    this.currentLocation = data;
    this.saveReadToDatabase(data);
  }

  saveReadToDatabase(data){
    this.db.getDatabaseState().subscribe(rdy => {
      if (rdy){
        this.db.saveReadings(data)
          .then((x)=>{console.log('New Reading Inserted',x)})
          .catch((e)=>{console.log(e)})
      }
    })
  }

  public watchGeolocation(){
    this.watchLoc = this.bk.on(BackgroundGeolocationEvents.location);
    this.watchLoc.subscribe((data)=>{
      console.log('Reading Data: ',data);
      this.storeGps(data);
      this.wb.next(this.wb.value+1)
    })
  }

  public getCurrentLoc(){
    const s = new Observable((subscriber)=>{
      const conf : BackgroundGeolocationCurrentPositionConfig = {enableHighAccuracy: true , timeout:10000 , maximumAge: 10000}
      this.bk.getCurrentLocation(conf).then(pos=>{
        console.log(pos)
        subscriber.next(pos)
        subscriber.complete()
      })
    })
    return s
  }

  public distance(coords1, coords2) {
    const { lat: lat1, lon: lon1 } = coords1;
    const { lat: lat2, lon: lon2 } = coords2;
    const degToRad = x => x * Math.PI / 180;
    const R = 6371;
    const halfDLat = degToRad(lat2 - lat1) / 2;
    const halfDLon = degToRad(lon2 - lon1) / 2;
    const a = Math.sin(halfDLat) * Math.sin(halfDLat) +
      Math.cos(degToRad(lat1)) * Math.cos(degToRad(lat2)) *
      Math.sin(halfDLon) * Math.sin(halfDLon);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    return R * c * 1000;
  }


}



