import {ElementRef, Injectable} from "@angular/core";
import {BehaviorSubject, Observable} from "rxjs";
import {switchMap, concatMap, startWith} from "rxjs/operators";
import {CustomerServiceProvider} from "../providers/customer-service/customer-service";
import {LoadingService} from "./loading";
import {Db} from "../providers/db/db";
import {HttpClient, HttpParams} from "@angular/common/http";

declare var google:any;

@Injectable()

export class MapService {
  public conf: any = {
    markerSvg : false,    // get marker icon from svg query
    showPloygon: true,    // show polygon
    attachInfo: true,     // show info window on markers
  };
  mapRef: ElementRef;
  map: any;
  currentArea: any;
  salesman: any;
  polling: any;
  pollingsc: any;
  pollingc: any;
  pollinga: any;
  customers: any;
  scannerCus:any;
  polygon: any;
  markers: any;
  bounds: any;
  activeInfoWindow: any;
  activeInfoWindowMarker: any;
  infoWindowFirstShow:number = 0;
  currentPos: any;
  ameen:any = {ameen:[] , sb:[] , newcus:[] };
  ameenC:any ;
  polyline: Record<string, any> = {};

  pollingGps:any;
  gpsPastPoints:any;
  gpsTodayPoints:any;
  snappedCoordinates:any = {today:[] , past:[]}
  strokColors = ['#0c4fe6' , '#E61315' , '#E2E626'];
  pointerInterval:any;
  rdyToFetch: BehaviorSubject<boolean> = new BehaviorSubject(false);
  goog:any = google;

  constructor(public waritex: CustomerServiceProvider , public ld: LoadingService , public db: Db , public http: HttpClient){

  }

  setArea(area){
    this.currentArea = area;
  }
  setMapRef(map){
    this.mapRef = map;
  }

  showMap(){
    const location1 = new google.maps.LatLng(33.248952,44.390661);
    const mapOptions = {
      zoom: 17,
      center: location1,
      fullscreenControl: false,
      mapTypeControl: false,
      streetViewControl: false,
    };

    this.map = new google.maps.Map(this.mapRef.nativeElement , mapOptions)
    this.map.setOptions({styles: [
        {
          "featureType": "poi",
          "elementType": "labels.text",
          "stylers": [
            {
              "visibility": "off"
            }
          ]
        },
        {
          "featureType": "poi.business",
          "stylers": [
            {
              "visibility": "off"
            }
          ]
        },
        {
          "featureType": "road",
          "elementType": "labels.icon",
          "stylers": [
            {
              "visibility": "off"
            }
          ]
        },
        {
          "featureType": "transit",
          "stylers": [
            {
              "visibility": "off"
            }
          ]
        }
      ]})
  }

  pollingCustomersData(showMarkers : boolean = true){
    let salesman = this.salesman;
    this.pollingc = Observable.of(1)
    //this.polling = Observable.interval(1000000000)
      .pipe(
        // startWith(0),
        switchMap(() => {
          this.ld.lpresent('customers').then(()=>{console.log('load customers');});
          return this.waritex.get_customers_by_areas(salesman);
        })
      )
      .subscribe(
        res => {
          // this.waritex.setAreas(res.res)
          this.customers = res.res[this.currentArea];
          this.ld.ldismiss('customers').then(()=>{console.log('dismiss customers');});
          this.showSteps(showMarkers);
        }, er => this.ld.ldismiss('customers')
        , () => {}
      );
  }

  pollingNewCustomersData(area , showMarkers : boolean = true){
    let salesman = this.salesman;
    this.pollingsc = Observable.interval(60*1000)
      .pipe(
        startWith(0),
        switchMap(() => {
          console.log('load n customers');
          // this.ld.lpresent('ncustomers').then(()=>{console.log('load n customers');});
          return this.waritex.get_new_customers_by_areas(salesman , area);
        })
      )
      .subscribe(
        res => {
          this.scannerCus = res.scanner;
          this.showMarkers(res.scanner , this.map , 'CustomerNameA' , 'newcus','Latitude','Longitude',3);
          // this.ld.ldismiss('ncustomers').then(()=>{console.log('dismiss n customers');});
        }, er => this.ld.ldismiss('ncustomers')
        , () => {}
      );
  }

  pollingAmeenData(area , opt:any = {ameen: false , sb:false}){
    let salesman = this.salesman;
    this.pollinga = Observable.of(1)
      .pipe(
        switchMap(() => {
          this.ld.lpresent('ameen').then(()=>{console.log('load Ameen customers');});
          return this.waritex.get_Ameen_by_areas_admin(salesman,area);
        })
      )
      .subscribe(
        res => {
          console.log(res);
          this.ameenC = res
          this.showAreaPolygon(res.polygon);
          const map_ameen = opt.ameen ? this.map : null;
          const map_sb = opt.sb ? this.map : null;
          this.showMarkers(res.dataAmeen , map_ameen , 'CustomerNameA' , 'ameen')
          this.showMarkers(res.dataSB , map_sb , 'CustomerNameA' , 'sb')
          this.ld.ldismiss('ameen').then(()=>{console.log('dismiss Ameen customers');});
          this.autoViewAll(this.ameen.ameen);
        }, er => this.ld.ldismiss('ameen')
        , () => {}
      );
  }

  showSteps(showMarkers:boolean = true){
    this.showAreaPolygon(this.customers[0].polypoints);
    this.showCustomers(showMarkers);
    this.reOpenInfoWindow();
    if (this.infoWindowFirstShow === 0) {
      this.infoWindowFirstShow++;
      this.autoViewAll();
    }
    this.getGpsReadingsFromApi()
  }

  showAreaPolygon(poly) {
    if (!this.conf.showPloygon)
      return false;
    const polygon = JSON.parse(poly);
    if (!polygon || polygon==null || polygon=='null' || polygon==undefined || polygon.length==0)
      return false;
    const p = new google.maps.Polygon({
      paths: polygon,
      strokeColor: "#FF0000",
      strokeOpacity: 0.8,
      strokeWeight: 2,
      fillColor: "#FF0000",
      fillOpacity: 0.15,
    });
    try{this.polygon.setMap(null)}
    catch (e) {console.log(e)}
    p.setMap(this.map);
    this.polygon = p;
  }

  showCustomers(showMarkers:boolean = true) {
    let customers = this.customers;
    this.markers = [];
    // init map's bounds
    this.bounds = new google.maps.LatLngBounds();
    // let j=0;
    for (let i = 0; i < customers.length; i++) {
      let positiono = new google.maps.LatLng(Number(customers[i].Lat), Number(customers[i].Lng));
      let marker = new google.maps.Marker({
        map: showMarkers ? this.map : null,
        position: positiono,
        title: customers[i].CustomerName,
      });

      this.MarkerSetIconSVG(marker , JSON.parse(customers[i].svg));
      // this.MarkersetIcon(marker , JSON.parse(customers[i].svg));

      this.bounds.extend(positiono);
      if (this.conf.attachInfo)
        this.attachInfo(marker, customers[i] , 'CustomerName');
      this.markers.push(marker);
    }
  }

  MarkersetIcon(marker , color = 1){
    // define icon images
    let yellow = 'assets/imgs/markeryellow.png';
    let red = 'assets/imgs/markerred.png';
    let green = 'assets/imgs/visitDoneNum.png';
    let url = yellow;
    if (color==2)
      url = red;
    else if (color==3)
      url = green;
    let point = new google.maps.Point(10,10);
    let icon = {
      url: url ,
      labelOrigin: point,
      scaledSize: new google.maps.Size(16, 16),
    };
    marker.setIcon(icon);
  }

  MarkerSetIconSVG(marker , svg=null  , color=null , opcity = null){
    let point = new google.maps.Point(0,-30);
    let icon = {
      path: 'M 0 0 C -2 -20 -10 -22 -10 -30 A 10 10 0 1 1 10 -30 C 10 -22 2 -20 0 0',
      fillColor: color!=null ? color:'#8b0000',
      fillOpacity: opcity!=null ? opcity : 1,
      strokeColor: '#000',
      strokeWeight: 1,
      scale: 0.85,
      labelOrigin: point,
    };
    if (this.conf.markerSvg && svg !== null){
      icon.path = !svg.path? icon.path : svg.path;
      icon.fillColor = !svg.fillColor? icon.fillColor : svg.fillColor;
      icon.scale = !svg.scale? icon.scale : svg.scale;
    }
    marker.setIcon(icon);
  }

  attachInfo(marker, customer , cust_name = 'CustomerNameA') {
    // var place = '<div dir="rtl" style="float: left;">&nbsp; &nbsp;<a href="https://www.google.com/maps/search/?api=1&query=' + customer.Lat + ',' + customer.Lng + '" target="_blank"> </a></div>';
    var infowindow = new google.maps.InfoWindow({
      content: '' +
        '<div><h4 style="float:right; margin-top: 0;" dir="rtl"> ' + customer[cust_name] + '&nbsp;&nbsp;&nbsp;</h4>' +
        // place +
        '</div>'
    });
    let flag = this;
    infowindow.addListener('closeclick' , function () {
      flag.activeInfoWindowMarker = null;
      flag.activeInfoWindow = null;
    });
    marker.addListener('click', function () {
      if (flag.activeInfoWindow) {
        flag.activeInfoWindow.close();
        flag.activeInfoWindowMarker = null;
        flag.activeInfoWindow = null;
      }
      infowindow.open(marker.get('map'), marker);
      flag.activeInfoWindow = infowindow;
      flag.activeInfoWindowMarker = marker
    });
  }

  // ReOpen InfoWindow
  reOpenInfoWindow(){
    if (this.activeInfoWindow)
      this.activeInfoWindow.open(this.map,this.activeInfoWindowMarker)
  }

  // Auto Zoom & Focus on Markers
  autoViewAll(markers = this.markers){
    this.bounds = new google.maps.LatLngBounds();
    for (var i = 0; i < markers.length; i++) {
      var loc = new google.maps.LatLng(markers[i].position.lat(), markers[i].position.lng());
      this.bounds.extend(loc);
    }
    this.map.fitBounds(this.bounds);       // auto-zoom
    this.map.panToBounds(this.bounds);     // auto-center
  }

  showMove(coords){
    var car = "M17.402,0H5.643C2.526,0,0,3.467,0,6.584v34.804c0,3.116,2.526,5.644,5.643,5.644h11.759c3.116,0,5.644-2.527,5.644-5.644 V6.584C23.044,3.467,20.518,0,17.402,0z M22.057,14.188v11.665l-2.729,0.351v-4.806L22.057,14.188z M20.625,10.773 c-1.016,3.9-2.219,8.51-2.219,8.51H4.638l-2.222-8.51C2.417,10.773,11.3,7.755,20.625,10.773z M3.748,21.713v4.492l-2.73-0.349 V14.502L3.748,21.713z M1.018,37.938V27.579l2.73,0.343v8.196L1.018,37.938z M2.575,40.882l2.218-3.336h13.771l2.219,3.336H2.575z M19.328,35.805v-7.872l2.729-0.355v10.048L19.328,35.805z";
    var icon = {
      path: car,
      scale: .7,
      strokeColor: 'white',
      strokeWeight: .10,
      fillOpacity: 1,
      fillColor: '#000000',
      offset: '5%',
      rotation: 0,
      anchor: new google.maps.Point(10, 25) // orig 10,50 back of car, 10,0 front of car, 10,25 center of car
    };
    let positiono = new google.maps.LatLng(coords.lat, coords.lng);
    try {
      var lastPosn = this.currentPos.position;
      this.currentPos.setPosition(positiono);
      var heading = google.maps.geometry.spherical.computeHeading(lastPosn, positiono);
      icon.rotation = heading;
      this.currentPos.setIcon(icon);
    }
    catch (e) {
      this.currentPos = new google.maps.Marker({
        position: positiono,
        map: this.map,
        title: '',
        icon: icon,
      });

    }
  }

  autoFocus(zoom: number = 18){
    try {
      this.map.panTo(this.currentPos.position);
      this.map.setZoom(zoom)
    }
    catch (e) {
      console.log(e);
    }
  }


  calculateHeading(lat1,lng1,lat2,lng2){
    var point1 = new google.maps.LatLng(lat1, lng1);
    var point2 = new google.maps.LatLng(lat2, lng2);
    var heading = google.maps.geometry.spherical.computeHeading(point1,point2);
    return heading;
  }

  getGpsReadingsFromApi(){
    this.pollingGps = Observable.of(1)
      .pipe(
        // startWith(0),
        switchMap((s) => {
          this.ld.lpresent('gps').then(()=>{console.log('load gps from api');});
          return this.waritex.get_GPS_by_areas(this.salesman , this.waritex.CurrentArea);
        }))
      .subscribe(
        res => {
          console.log(res)
          this.gpsPastPoints = res.past
          this.gpsTodayPoints = res.today
          this.ld.ldismiss('gps').then(()=>{console.log('dismiss load gps from api');});
          this.startPolylines()
        }, er => this.ld.ldismiss('gps')
        , () => {}
      );
  }

  startPolylines(){
    var ch = this.chunkGPSPoints(this.gpsPastPoints)
    var s = []
    for (var i=0 ; i < ch.length ; i++) {
      s.push(this.prepareRequestPoints(ch[i]))
    }
    if( s[0].length > 0 )
      this.handleRequests(s , 'past')
    var cht = this.chunkGPSPoints(this.gpsTodayPoints)
    var st = []
    for (var it=0 ; it < cht.length ; it++) {
      st.push(this.prepareRequestPoints(cht[it]))
    }
    if( st[0].length > 0 )
      this.handleRequests(st , 'today')
    else {
      this.rdyToFetch.next(true);
    }
  }

  chunkGPSPoints(gpsPoints){
    var res = [];
    var gps = gpsPoints;
    const chunkSize = 100;
    const overlap = 1;
    if(gps.length > 100){
      for (let i = 0; i < gps.length; i += chunkSize) {
        if(i==0) // first chunk (all 100 points)
          res.push(gps.slice(i, i + chunkSize));
        else if(i-overlap+chunkSize >= gps.length) // last chunk (all points)
          res.push(gps.slice(i-overlap, i + chunkSize));
        else // other chunks (98-->198 etc...)
          res.push(gps.slice(i-overlap, i-overlap + chunkSize));
      }
    }
    else
      res.push(gps.slice(0, chunkSize));
    return res;
  }
  prepareRequestPoints(req) {
    var pathValues = [];
    for (var i=0 ; i < req.length ; i++) {
      pathValues.push(req[i].lat + ',' + req[i].lng);
    }
    return pathValues;
  }
  handleRequests(requests , type){
    const s = Observable.range(0, requests.length)
      .pipe(
        concatMap((r) => {
          console.log('request '+type+' #',r)
          return this.runSnapToRoad(requests[r])
        }))
      .subscribe((res) => {
          console.log('res api: ' ,res)
          this.processSnapToRoadResponse(res, type);
        }
        , (e) => {
          console.log(e)
          alert('Error Try Again...')
        }
        , () => {
          console.log('completed....................');
          console.log(type , this.snappedCoordinates[type]);
          this.snappedCoordinates[type] = [].concat.apply([], this.snappedCoordinates[type]);
          console.log(type +' after concat',this.snappedCoordinates[type]);
          this.drawSnappedPolyline(type)
          if (type=='today'){
            console.log(this.rdyToFetch)
            this.rdyToFetch.next(true);
            console.log(this.rdyToFetch)
          }
        }
      )
    return s;
  }
  runSnapToRoad(path) {
    const url = 'https://roads.googleapis.com/v1/snapToRoads';
    const opt = new HttpParams()
      .set('interpolate','true')
      .set('key','AIzaSyAyHOguHswKUpy_jhC4EGrhe527y-xU7l0')
      .set('path',path.join('|'));
    return this.http.get(url , {params:opt})
  }
  processSnapToRoadResponse(data , place = 'past') {
    let subSnapped = []
    for (var i = 0; i < data.snappedPoints.length; i++) {
      var latlng = new google.maps.LatLng(
        data.snappedPoints[i].location.latitude,
        data.snappedPoints[i].location.longitude);
      subSnapped.push(latlng);
    }
    this.snappedCoordinates[place].push(subSnapped);
  }
  drawSnappedPolyline(type) {
    /*
    var car = "M17.402,0H5.643C2.526,0,0,3.467,0,6.584v34.804c0,3.116,2.526,5.644,5.643,5.644h11.759c3.116,0,5.644-2.527,5.644-5.644 V6.584C23.044,3.467,20.518,0,17.402,0z M22.057,14.188v11.665l-2.729,0.351v-4.806L22.057,14.188z M20.625,10.773 c-1.016,3.9-2.219,8.51-2.219,8.51H4.638l-2.222-8.51C2.417,10.773,11.3,7.755,20.625,10.773z M3.748,21.713v4.492l-2.73-0.349 V14.502L3.748,21.713z M1.018,37.938V27.579l2.73,0.343v8.196L1.018,37.938z M2.575,40.882l2.218-3.336h13.771l2.219,3.336H2.575z M19.328,35.805v-7.872l2.729-0.355v10.048L19.328,35.805z";
    var icon = {
      path: car,
      scale: .7,
      strokeColor: 'white',
      strokeWeight: .10,
      fillOpacity: 1,
      fillColor: '#000000',
      offset: '5%',
      rotation: 0,
      anchor: new google.maps.Point(10, 25) // orig 10,50 back of car, 10,0 front of car, 10,25 center of car
    };
    */
    this.polyline[type] = null;
    var snappedPolyline = new google.maps.Polyline({
      path: this.snappedCoordinates[type],
      strokeColor: type=='past' ? this.strokColors[0] : this.strokColors[1] ,
      strokeWeight: 2,
      strokeOpacity: 0.9,
      icons: [
        {
          icon: {path: ''},
          offset: "100%",
        },
      ],
    });

    this.polyline[type] = snappedPolyline;

    snappedPolyline.setMap(this.map);
    
    // this.animateCircle(snappedPolyline)
    
    this.map.addListener("zoom_changed", () => {
      clearInterval(this.pointerInterval);
      snappedPolyline.set("icons", snappedPolyline.get("icons").slice(0, 1))
      // this.animateCircle(snappedPolyline)
      
      this.changeStroke(snappedPolyline)
      
    });
  }
  
  animateCircle(line) {
    function scale (number, inMin, inMax, outMin, outMax) {
      return (number - inMin) * (outMax - outMin) / (inMax - inMin) + outMin;
    }
    const lineSymbol_s = {
      path: 'M 0 0 H 2 M 0 0.01 H 2 M 0 -0.01 H 2' ,
      strokeColor: "#faf1ff",
      strokeOpacity: 0.7,
      rotation: 90,
    };
    const icons = line.get("icons");
    let z = this.map.getZoom();
    console.log("Zoom: ",this.map.getZoom())
    let points = scale(z,8,22,10,350)+'px' ;
    let offsets = {10:500 , 11:1000 , 12:2000 , 13:4000 , 14:6000 , 15:8000 , 16:10000 , 17:20000 , 18:40000 , 19:80000 , 20:130000 , 21:150000 , 22:190000}
    let offsetMove = offsets[z] ;
    let speed = 20 ;
    console.log('points: ',points , 'oofsetMove: ',offsetMove , 'speed' ,speed )
    icons.push({icon: lineSymbol_s, offset: "0%", repeat: points})
    var d = 0
    this.pointerInterval = window.setInterval(() => {
      d = (d + 1) % (offsetMove);
      icons[1].offset = 100 * (d / offsetMove) + "%";
      line.set("icons", icons);
    }, speed );

  }
  
  changeStroke(polyline){
    let z = this.map.getZoom();
    if (z < 16)
      polyline.set("strokeWeight" , 2)
    else
      polyline.set("strokeWeight" , 4)
  }

  clearGmapService(){
    try {
      try {
        this.pollingsc.unsubscribe();
      }catch (e) {
        console.log(e)
      }
      this.map = null
      this.currentArea = null
      this.salesman = null
      // this.map.pollingc.unsubscribe();
      // this.map.pollinga.unsubscribe();
      this.pollingc = null
      this.pollinga = null
      this.polyline = {};
      this.customers = null
      this.polygon = null
      this.markers = null
      this.bounds = null
      this.activeInfoWindow = null
      this.activeInfoWindowMarker = null
      this.infoWindowFirstShow = 0;
      this.currentPos = null
      this.pollingGps = null
      this.gpsPastPoints = null
      this.gpsTodayPoints = null
      this.snappedCoordinates = {today:[] , past:[]}
      this.pointerInterval = null
      this.rdyToFetch = new BehaviorSubject(false);
      this.scannerCus = null
    }
    catch (e) {
      console.log(e)
    }
  }

  drawSnappedPolyline1(type) {
    for (var x=0; x<this.snappedCoordinates[type].length;x++){
      var snappedPolyline = new google.maps.Polyline({
        path: this.snappedCoordinates[type][x],
        strokeColor: x%2==0 ? this.strokColors[0] : this.strokColors[1] ,
        strokeWeight: 2,
        strokeOpacity: 0.9,
        icons: [
          {
            icon: {path: ''},
            offset: "100%",
          },
        ],
      });
      snappedPolyline.setMap(this.map);
    }


  }

  showMarkers(array , map=null  , title='CustomerNameA',x, lat='Latitude' , lon='Longitude' , color = null) {
    try {
      console.log('try trash all markers...')
      if (this.ameen[x].length > 0 ){
        // hide markers
        console.log('try hide all markers...')
        this._hideMarkers(this.ameen[x])
        // delete them
        console.log('try delete all markers...')
        this.ameen[x] = []
      }
    }
    catch (e) {console.log(e)}
    let customers = array;
    this.ameen[x] = [];
    // init map's bounds
    this.bounds = new google.maps.LatLngBounds();
    // let j=0;
    for (let i = 0; i < customers.length; i++) {
      let positiono = new google.maps.LatLng(Number(customers[i][lat]), Number(customers[i][lon]));
      let marker = new google.maps.Marker({
        map: map,
        position: positiono,
        title: customers[i][title],
      });

      this.MarkerSetIconSVG(marker , null , x=='ameen'? 'yellow':'green' , 1);
      // this.MarkersetIcon(marker , color );

      this.bounds.extend(positiono);
      if (this.conf.attachInfo)
        this.attachInfo(marker, customers[i]);
      this.ameen[x].push(marker);
    }
  }


  _hideMarkers(markers): void {
    this._setMapOnAll(null , markers);
  }
  _showMarkers(markers): void {
    this._setMapOnAll(this.map , markers);
  }
  _setMapOnAll(map,markers) {
    for (let i = 0; i < markers.length; i++) {
      markers[i].setMap(map);
    }
  }

  _hidePolyline(polyline): void {
    try {
      this.polyline[polyline].setVisible(false)
    }
    catch (e) {
      console.log(e);
    }
  }

  _showPolyline(polyline): void {
    try {
      this.polyline[polyline].setVisible(true)
    }
    catch (e) {
      console.log(e);
    }
  }




  }



