import { Component } from '@angular/core';
import {ActionSheetController, NavController} from 'ionic-angular';
import {CustomerServiceProvider} from "../../../providers/customer-service/customer-service";
import {AuthServiceProvider} from "../../../providers/auth-service/auth-service";
import {AreaMap} from "./areaMap/areaMap";


@Component({
  selector: 'page-areas',
  templateUrl: 'areasSales.html'
})
export class AreasSalesPage {

  areas:any = [];
  salesman:any;
  salesman_name:any;
  loading:boolean = false;
  totalCus:any=0;
  totalRedVis:any=0;
  totalSRedVis:any=0;
  totalGreenVis:any=0;
  totalLGreenVis:any=0;
  totalOrangeVis:any=0;
  AvgVisits:any=0;
  AvgShould:any=0;
  Remain:boolean = false;


  constructor(public navCtrl: NavController , public waritex: CustomerServiceProvider , public auth: AuthServiceProvider, public actionSheetCtrl: ActionSheetController ) {
    this.salesman = this.auth.supervisor? this.auth.selectedSalesman : this.auth.getUserInfo();
  }

  ionViewWillEnter(){
    this.salesman = this.auth.supervisor? this.auth.selectedSalesman : this.auth.getUserInfo();
    this.salesman_name = this.auth.getSalesmanName()
    this.startUp();
  }

  startUp(){
    this.totalCus=0;
    this.totalRedVis=0;
    this.totalSRedVis=0;
    this.totalGreenVis=0;
    this.totalLGreenVis=0;
    this.totalOrangeVis=0;
    this.getData();
  }

  getData(){
    if (!this.auth.supervisor && this.areas.length>0) {
      this.getCountTotal();
      return;
    }
    if (this.auth.supervisor && !this.auth.selectedSalesman)
      return;
    this.loading = true;
    this.waritex.get_customers_by_areas(this.salesman).subscribe((res) => {
      this.waritex.setAreas(res.res);
      try {
        this.waritex.setAvgs(res.avgs[0])
        this.AvgVisits = res.avgs[0].AvgVisits
        if ( res.avgs[0].RemainWokingDays == 0 ){
          this.Remain = true;
        }
        this.AvgShould = res.avgs[0].AvgShould
      }catch (e) {console.log(e)}
      this.areas = Object.keys(res.res);
      this.loading = false;
      this.getCountTotal();
    } , err => {this.loading = false; this.navCtrl.pop()})
  }

  goArea(area){
    console.log(area)
    this.navCtrl.push(AreaMap, {area: area , counters:this.countNotVisited(area)});
  }

  old__countNotVisited(area){
    let areav = this.waritex.Areas[area]
    let redVisit = [];
    let sRedVisit = [];
    let gVisit = [];
    let lgVisit = [];
    let orgVisit = [];
    areav.forEach(item =>{
      if (item.visited==0)
        sRedVisit.push(item);
      else if (item.LastVisitDate > 15)
        gVisit.push(item);
      else if (item.distance > 100)
        redVisit.push(item);
      else if (item.opened==1)
        lgVisit.push(item);
      else if (item.opened==null)
        orgVisit.push(item);
    })
    return {sred: sRedVisit , red: redVisit , gre:gVisit , lgre:lgVisit , org:orgVisit}
  }

  countNotVisited(area){
    let areav = this.waritex.Areas[area]
    let redVisit = [];
    let sRedVisit = [];
    let gVisit = [];
    let lgVisit = [];
    let orgVisit = [];
    areav.forEach(item =>{
      if (item.visited==0)
        redVisit.push(item);
      else
        lgVisit.push(item);
    })
    return {sred: sRedVisit , red: redVisit , gre:gVisit , lgre:lgVisit , org:orgVisit}
  }

  old__getCountTotal(){
    for (let i=0; i<this.areas.length;i++){
      this.totalCus = this.totalCus + this.waritex.Areas[this.areas[i]].length;
      this.totalRedVis = this.totalRedVis + this.countNotVisited(this.areas[i]).red.length;
      this.totalSRedVis = this.totalSRedVis + this.countNotVisited(this.areas[i]).sred.length;
      this.totalGreenVis = this.totalGreenVis + this.countNotVisited(this.areas[i]).gre.length;
      this.totalLGreenVis = this.totalLGreenVis + this.countNotVisited(this.areas[i]).lgre.length;
      this.totalOrangeVis = this.totalOrangeVis + this.countNotVisited(this.areas[i]).org.length;
    }
    this.getTodayArea();
  }

  getCountTotal(){
    for (let i=0; i<this.areas.length;i++){
      this.totalCus = this.totalCus + this.waritex.Areas[this.areas[i]].length;
      this.totalRedVis = this.totalRedVis + this.countNotVisited(this.areas[i]).red.length;
      this.totalLGreenVis = this.totalLGreenVis + this.countNotVisited(this.areas[i]).lgre.length;
    }
    this.getTodayArea();
  }

  supervisor(){
    let buttons = this.makeButtons();
    const actionSheet = this.actionSheetCtrl.create({
      title: ' ',
      buttons: buttons
    });
    actionSheet.present();
  }

  makeButtons(){
    let buttons = [];
    let salesmans = this.auth.salesmans;
    for(let i=0; i<salesmans.length; i++){
      buttons.push({
        text: salesmans[i].name,
        role: 'destructive',
        handler: () => {
          this.salesman = salesmans[i].code;
          this.salesman_name = salesmans[i].name;
          this.auth.selectedSalesman = this.salesman;
          this.startUp();
        }
      });
    }
    return buttons;
  }

  getTodayArea(){
    this.areas.forEach( area => {
      let s = this.waritex.Areas[area].find( cus => {
        return cus.LastVisitDate==0
      })
      if (s){
        this.waritex.setToday(s.city)
      }
    })
    console.log(this.waritex.today)
  }

}



