import { Component } from '@angular/core';
import { LoadingController, Loading , AlertController , NavController, NavParams} from 'ionic-angular';
import { AuthServiceProvider } from "../../providers/auth-service/auth-service";
import {TabsPage} from "../tabs/tabs";
import {TabsSalesPage} from "../tabs/tabsSales";

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  username:string;
  password:string;
  loading: Loading;

  constructor(public nav: NavController, public navParams: NavParams , private auth: AuthServiceProvider, private alertCtrl: AlertController, private loadingCtrl: LoadingController) {
  }

  ionViewDidLoad() {

  }

  login() {
    this.showLoading()
    this.auth.login(this.username, this.password).subscribe(allowed => {
        if (allowed) {
          this.loading.dismiss();
          if (allowed.scanner && (allowed.scanner===1 || allowed.scanner==='1') && (allowed.groups!=='' && allowed.groups!==null)){
            this.showScannerAlert();
          }
          else{
            this.auth.currentType = 'sales';
            this.nav.setRoot(TabsSalesPage);
          }

        } else {
          this.showError("Access Denied");
        }
      },
      error => {
        this.showError(error);
      });
  }

  showLoading() {
    this.loading = this.loadingCtrl.create({
      content: 'Please wait...',
      dismissOnPageChange: true
    });
    this.loading.present();
  }

  showError(text) {
    this.loading.dismiss();

    let alert = this.alertCtrl.create({
      title: 'Authentication Failed',
      subTitle: text,
      buttons: ['OK']
    });
    alert.present();
  }

  showScannerAlert() {
    let alert = this.alertCtrl.create();
    alert.setTitle('  ');
    alert.addInput({
      type: 'radio',
      label: '',
      value: 'sales',
      checked: true
    });
    alert.addInput({
      type: 'radio',
      label: '',
      value: 'scanner',
      checked: false
    });
    alert.addButton({
      text: 'OK',
      handler: data => {
        this.auth.currentType = data;
        if (data=='scanner')
          this.nav.setRoot(TabsPage);
        else if (data=='sales')
          this.nav.setRoot(TabsSalesPage);
      }
    });
    alert.present();
  }


}



