import { Injectable } from '@angular/core';
import {BehaviorSubject} from "rxjs";
import {SQLite, SQLiteObject} from "@ionic-native/sqlite";
import {AlertController, Platform} from "ionic-angular";
import {File} from "@ionic-native/file";
import {LoadingService} from "../../utils/loading";

@Injectable()

export class Db {
  public dbName: string = 'wrsc.db';
  public database: SQLiteObject;
  public dbReady: BehaviorSubject<boolean> = new BehaviorSubject(false);

  constructor(private sqlite: SQLite, private plt: Platform, public file: File , public ld: LoadingService , public alertCtrl:AlertController){
    this.ld.present().then(()=>{console.log('load DB');});
    this.plt.ready().then(() => {
      if(this.plt.is('android')){
        this.readDB();
        this.getDatabaseState().subscribe(ready=>{
          this.ld.dismiss().then(()=>{console.log('dismiss db');});
        })
      }
      else {
        this.dbReady.next(false);
        this.ld.dismiss().then(()=>{console.log('dismiss db');});
      }
    })
  }

  public getDatabaseState() {
    return this.dbReady.asObservable();
  }

  private async readDB(f=0){
    return await this.sqlite.create({
      name: this.file.externalRootDirectory.substring(7)+this.dbName,
      location: 'default',
    })
      .then((db: SQLiteObject) => {
        console.log('Success Read From SDCard DB')
        this.database = db;
        this.SQL(db)
          .then((x)=>{
            console.log('Created Table...' , x);
            this.dbReady.next(true);
          })
          .catch((e) => {
            console.log('Error in Execute SQL')
            console.log(e)
          })
      })
      .catch(e => {
        console.log(e)
        this.showError('Error In Permissions',404)
        this.dbReady.next(false)
      });
  }

  private showError(text,status) {
    let alert = this.alertCtrl.create({
      title: 'Error Status: '+status,
      subTitle: text,
      buttons: ['OK']
    });
    alert.present();
  }


  private SQL(db: SQLiteObject){
    console.log('Try Creating Tables...')
    console.log(db)
    const SQL = 'CREATE TABLE IF NOT EXISTS "gps" ("datetime" REAL, "salesman" TEXT, "lat" REAL, "lng" REAL, "synced" INTEGER DEFAULT 0);';
    return db.executeSql(SQL,[]);
  }

  public async getReadings(){
    const promise = new Promise(((resolve, reject) => {
      this.database.executeSql('SELECT * FROM gps WHERE synced = 0 ORDER BY datetime asc',[])
        .then((data)=>{
          const gps = [];
          if (data.rows.length > 0) {
            for (let i = 0; i < data.rows.length; i++) {
              gps.push({
                datetime: data.rows.item(i).datetime,
                salesman: data.rows.item(i).salesman,
                lat: data.rows.item(i).lat,
                lng: data.rows.item(i).lng,
                synced: data.rows.item(i).synced,
              });
            }
          }
          return resolve(gps);
        })
        .catch((e) => console.log(e))
    }))
    return await promise;
  }

  public async syncReadings(){
    const promise = new Promise(((resolve, reject) => {
      this.database.executeSql('UPDATE gps SET synced = 1 WHERE synced = 0',[])
        .then((data)=>{
          return resolve(true);
        })
        .catch((e) => console.log(e))
    }))
    return await promise;
  }


  public async saveReadings(data){
    const promise = new Promise(((resolve, reject) => {
      const b = [];
      b.push(data.time)
      b.push(data.salesman);
      b.push(data.latitude);
      b.push(data.longitude);
      b.push(0);
      const sql = ' INSERT INTO gps (datetime , salesman , lat , lng , synced) VALUES (?,?,?,?,?) ';
      this.database.executeSql(sql, b)
        .then((x)=> resolve(x) )
        .catch((e) => console.log(e))
    }))
    return await promise;
  }

  public async getNewReadings(date){
    const promise = new Promise(((resolve, reject) => {
      this.database.executeSql("SELECT * FROM gps WHERE datetime(gps.datetime/1000,'unixepoch','localtime') > datetime((?)/1000,'unixepoch','localtime') ORDER BY gps.datetime asc",[date])
        .then((data)=>{
          console.log('date: ',date)
          console.log('dataaa: ',data)
          const gps = [];
          if (data.rows.length > 0) {
            for (let i = 0; i < data.rows.length; i++) {
              gps.push({
                datetime: data.rows.item(i).datetime,
                salesman: data.rows.item(i).salesman,
                lat: data.rows.item(i).lat,
                lng: data.rows.item(i).lng,
                synced: data.rows.item(i).synced,
              });
            }
          }
          return resolve(gps);
        })
        .catch((e) => console.log(e))
    }))
    return await promise;
  }


}



